.list {
    width: 327px;
    margin: 0 auto;
    margin-bottom: 25px;
    padding:0;
}

.item {
    display: flex;
     justify-content: left;
  align-items: center;
    margin-bottom: 17px;
    width:100%;
box-shadow: 1px 0px 4px rgb(0 0 0 / 12%), 2px 2px 1px rgb(0 0 0 / 14%), 2px 3px 3px rgb(0 0 0 / 20%);
}

.online {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: green;
    margin-left: 5px;
    margin-right: 15px;
}

.offline {
background-color:red;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-left: 5px;
    margin-right: 15px;
}

.name {
    font-weight: 600;
    font-size: 16px;
    margin-left: 10px;
}