.profile {
    padding:0;
    width: 250px;
    margin: 0 auto;
    border: 1px solid rgba(120,120,120,25%);
    border-radius: 5px;
    box-shadow: 0px 6px 17px rgb(0 0 0 / 12%), 4px 6px 10px rgb(0 0 0 / 14%), 0px 5px 8px rgb(0 0 0 / 20%);
}

.description {
    text-align: center;
    padding: 30px 15px;
     align-items: center;
  width: 250px;
}

.avatar {
    border-radius: 50%;
}


.name {
        font-size: 26px;
}

.tag {
    color: rgb(125, 125, 133);
}

.location {
        margin-bottom: 0;
        color: rgb(125, 125, 133);
}

.stats {
display: flex;
  width: 250px;
    background-color: whitesmoke;
}

.item {
  text-align: center;
  display:block;
  border: 1px solid rgba(120,120,120,25%);
  padding: 20px 10px;
  font-size: 14px;
  width: 100%;
}

.label {
  display: block;
  color: rgb(125, 125, 133);
}

.quantity{
    font-weight: 700;
}